.contact__info {
  background-color: var(--clr-secondary-5);
  width: 100%;
  height: 200vh;
  padding-top: 6vh;
}

.contact__section {
  width: 90%;
  height: 42%;
  margin: 6vh auto;
  background-color: var(--clr-secondary-5);
  box-shadow: var(--neumorphism-secondary-5);
  border-radius: 2rem;
}

.contact__title {
  height: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  color: var(--clr-primary-1);
}

.contact__body {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.contact__item {
  border: 1px solid var(--clr-primary-8);
  border-radius: 1rem;
  margin: 1rem;
  color: var(--clr-primary-1);
  font-size: 2.2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
  text-decoration: none;
}

.contact__item:hover {
  color: var(--clr-primary-3);
}

@media screen and (min-width: 768px) {
  .contact__info {
    height: 100vh;
    padding: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .contact__section {
    width: 45%;
    height: 90%;
    margin: 0;
  }

  .contact__item:hover {
    color: var(--clr-primary-3);
  }
}

@media screen and (min-width: 1100px) {
  .contact__item {
    font-size: 3rem;
  }
}
